module.exports = "import React from 'react';\nimport PropTypes from 'prop-types';\nimport classNames from 'classnames';\nimport {withStyles} from '@material-ui/core/styles';\nimport AppBar from '@material-ui/core/AppBar';\nimport Toolbar from '@material-ui/core/Toolbar';\nimport IconButton from '@material-ui/core/IconButton';\nimport MenuIcon from '@material-ui/icons/Menu';\nimport Typography from '@material-ui/core/Typography';\nimport Button from '@material-ui/core/Button';\nimport Fab from '@material-ui/core/Fab';\nimport AddIcon from '@material-ui/icons/Add';\nimport Snackbar from '@material-ui/core/Snackbar';\n\nconst styles = theme => ({\n    root           : {\n        position: 'relative',\n        overflow: 'hidden'\n    },\n    appFrame       : {\n        width          : 360,\n        height         : 360,\n        backgroundColor: theme.palette.background.paper\n    },\n    menuButton     : {\n        marginLeft : -12,\n        marginRight: 20\n    },\n    button         : {\n        marginBottom: theme.spacing.unit\n    },\n    fab            : {\n        position: 'absolute',\n        bottom  : theme.spacing.unit * 2,\n        right   : theme.spacing.unit * 2\n    },\n    fabMoveUp      : {\n        transform : 'translate3d(0, -46px, 0)',\n        transition: theme.transitions.create('transform', {\n            duration: theme.transitions.duration.enteringScreen,\n            easing  : theme.transitions.easing.easeOut\n        })\n    },\n    fabMoveDown    : {\n        transform : 'translate3d(0, 0, 0)',\n        transition: theme.transitions.create('transform', {\n            duration: theme.transitions.duration.leavingScreen,\n            easing  : theme.transitions.easing.sharp\n        })\n    },\n    snackbar       : {\n        position: 'absolute'\n    },\n    snackbarContent: {\n        width: 360\n    }\n});\n\nclass FabIntegrationSnackbar extends React.Component {\n    state = {\n        open: false\n    };\n\n    handleClick = () => {\n        this.setState({open: true});\n    };\n\n    handleClose = () => {\n        this.setState({open: false});\n    };\n\n    render()\n    {\n        const {classes} = this.props;\n        const {open} = this.state;\n        const fabClassName = classNames(classes.fab, open ? classes.fabMoveUp : classes.fabMoveDown);\n\n        return (\n            <div className={classes.root}>\n                <Button className={classes.button} onClick={this.handleClick}>\n                    Open snackbar\n                </Button>\n                <div className={classes.appFrame}>\n                    <AppBar position=\"static\" color=\"primary\">\n                        <Toolbar>\n                            <IconButton className={classes.menuButton} color=\"inherit\" aria-label=\"Menu\">\n                                <MenuIcon/>\n                            </IconButton>\n                            <Typography variant=\"h6\" color=\"inherit\">\n                                Out of my way!\n                            </Typography>\n                        </Toolbar>\n                    </AppBar>\n                    <Fab color=\"secondary\" className={fabClassName}>\n                        <AddIcon/>\n                    </Fab>\n                    <Snackbar\n                        open={open}\n                        autoHideDuration={4000}\n                        onClose={this.handleClose}\n                        ContentProps={{\n                            'aria-describedby': 'snackbar-fab-message-id',\n                            className         : classes.snackbarContent\n                        }}\n                        message={<span id=\"snackbar-fab-message-id\">Archived</span>}\n                        action={\n                            <Button color=\"inherit\" size=\"small\" onClick={this.handleClose}>\n                                Undo\n                            </Button>\n                        }\n                        className={classes.snackbar}\n                    />\n                </div>\n            </div>\n        );\n    }\n}\n\nFabIntegrationSnackbar.propTypes = {\n    classes: PropTypes.object.isRequired\n};\n\nexport default withStyles(styles)(FabIntegrationSnackbar);\n"